/*
  =====================
  Main App
  =====================
*/
html {
  height: 100%; }

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100%; }

#root {
  padding-bottom: 15px; }

.site-content {
  -ms-flex: 1 1;
      flex: 1 1; }

.input-field {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; }

.the-grid {
  padding-top: 50px; }

img {
  border-radius: 5%; }

/*
  =====================
  View & Websites Component
  =====================
*/
.genres {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between; }

@-webkit-keyframes zoom {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes zoom {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(1);
            transform: scale(1); } }

.website-icons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around; }
  .website-icons a {
    color: inherit;
    text-decoration: none; }
    .website-icons a:hover {
      opacity: 0.8; }

/*
  =====================
  Screenshot Component
  =====================
*/
.screenshots img:hover {
  opacity: 0.8;
  cursor: pointer; }

.zoomBlock {
  display: none;
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9);
  /* Black w/ opacity */ }

.zoom-content {
  margin: auto;
  display: block;
  width: 90%;
  max-width: 700px; }

.zoom-content {
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s; }

.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }
  .close:hover {
    cursor: pointer;
    opacity: 0.8; }

/*
  =====================
  Games Component
  =====================
*/
.pop {
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }
  .pop:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05); }

/*
  =====================
  Navbar Component
  =====================
*/
.active {
  font-weight: bold; }

.navlink-item {
  color: #a29d9a; }
  .navlink-item:hover {
    color: #6f6a67; }

/*
  =====================
  Rating Component
  =====================
*/
.progress-ring__circle {
  -webkit-transition: 1.35s stroke-dashoffset ease-in;
  -o-transition: 1.35s stroke-dashoffset ease-in;
  transition: 1.35s stroke-dashoffset ease-in;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%; }

.ratings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between; }

/*
  =====================
  Footer
  =====================
*/
.footer {
  margin-top: auto;
  position: relative;
  padding: 20px; }
  .footer p,
  .footer a {
    text-decoration: none;
    color: #a29d9a; }
    .footer p:hover,
    .footer a:hover {
      color: #6f6a67; }



